import React from "react"
import classes from "./TopBar.css"
import { Aux } from "../../../Components/Hocs/Aux"

export const TopBar = () => {
    return(
        <Aux>
            <div>
                <div>
                    <img src="flipboard_logo.png" alt="logo" />
                    <h1 className={classes.top_bar}>FLIPBOARD_MIMIC</h1>
                </div>
            </div>
        </Aux>
    )
}